import AuthService from 'Store/actions/AuthService'
import {snackbarError} from 'Store/actions/snackbar'

export default function(errors){
  console.error("Errors", errors)
  try{
    if(errors.length > 0){
      showError(errors)
    }else if(errors.message !== undefined) {
      snackbarError(errors.message, Date.now())
    }
  }catch(err){
    //console.log("caught an expception")
    //console.log(errors, err)
    snackbarError("Unknown error occured", Date.now())
  }
}

function showError(errors){
  //console.log(errors)
  //console.log("Must show errors")
  if(errors === undefined){
    return snackbarError("Unknow error occured", Date.now())
  }
  for(let i = 0; i < errors.length; i++){
    
    if(errors[i].message !== undefined ){
      if(errors[i].message === "Unauthorized"){
        console.log("Now will logout user")
        snackbarError("Hi! you're not logged in.", Date.now())
        AuthService.logout()
      }else{
        snackbarError(errors[i].message, Date.now())
      }
    }
  }
}