import React from 'react'
import TextField from '@material-ui/core/TextField'
import Button from 'Components/LoadingButton'
import graphql from 'babel-plugin-relay/macro'
import CommitRelay from 'Components/CommitRelay'
import FormRegular from 'Components/Styled/FormRegular'
import { snackbarInfo } from 'Store/actions/snackbar'

const getUserMutation = graphql`
  mutation GetUserMutation($data: GetUserInput!) {
    getUser(data: $data){
      user {
        username
        _id
        walletBalance
        bonus
      }
    }
  }
`

function GetUser(props) {
  let usernameRef = React.useRef()
  let [requesting, setRequesting] = React.useState(false)
  function handleSubmit(e) {
    e.preventDefault()
    let username = usernameRef.current.value
    if(!username) {
      snackbarInfo("Enter username")
    }
    setRequesting(true)
    CommitRelay({data: {username}}, getUserMutation, (response, error) => {
      setRequesting(false)
      if(!error) {
        props.userResult(response)
      }
    })
  }

  return <FormRegular onSubmit={handleSubmit} component={"form"}>
    <TextField type="email" inputRef={usernameRef} label="Username" required fullWidth/>
    <Button isLoading={requesting} type="submit">Get User</Button>
  </FormRegular>
}

export default GetUser