import React from 'react'
import {withRouter, Route, useHistory} from 'react-router-dom'
import { Paper, Typography, Button, Divider } from '@material-ui/core'
import Fab from 'Components/Fab'
import {makeStyles} from '@material-ui/core/styles'
import AddTournament from './AddTournament/AddTournament'
import {createFragmentContainer} from 'react-relay'
import graphql from 'babel-plugin-relay/macro'
import CustomList from 'Components/CustomList'
import makeDate from 'Utils/MakeDate'
import Delete from '@material-ui/icons/Delete'
import IconButton from '@material-ui/core/IconButton'
import CommitRelay from 'Components/CommitRelay'
import { snackbarSuccess } from 'Store/actions/snackbar'
import PushPlayers from './PushPlayers/PushInTournament'

const deleteTournamentMutation = graphql`
  mutation TournamentsInRoundMutation($data: DeleteTournamentInMegladronInput!){
    deleteTouramentInMegladron(data: $data) {
      _id
      rounds {
        title
        _id
        totalMatches
        tournaments {
          _id
          title
        }
      }
    }
  }
`

const styles = makeStyles(theme => ({
  paper: {
    marginBottom: theme.spacing(1),
    position: "relative",
    padding: theme.spacing(1)
  },
  title: {
    textTransform: "uppercase",
    fontSize: 24,
    display: "inline-block",
    marginBottom: theme.spacing(1)
  }
}))

function makeList(tournaments, deleteClick) {
  if(!tournaments){
    return []
  }
  return tournaments.map((t, index) => ({
    id: t._id,
    primaryText: t.title,
    icon: (index + 1),
    secondaryText: makeDate(t.time),
    secondaryIcon: <IconButton onClick={() => deleteClick(t._id)}><Delete /></IconButton>
  }))
}

function TournamentsInRounds(props){
  const classes = styles()
  const history = useHistory()
  const items = makeList(props.round.tournaments, (id) => deleteClick(id, props.round.title))

  function deleteClick(tournamentId, roundTitle) {
    console.log(tournamentId, roundTitle)
    CommitRelay({data: {tournamentId, roundTitle, megladronId: props.match.params.id}}, deleteTournamentMutation, (response, error) => {
      if(!error) {
        snackbarSuccess("Tournament Deleted")
      }
    })
  }

  function itemClick(item) {
    history.push('/tournaments/'+item.id)
  }

  return (
    <Paper className={classes.paper}>
      { props.round.totalMatches !== props.round.tournaments.length && <Typography variant="h4">Need to make another {props.round.totalMatches - props.round.tournaments.length} tournaments to get divider players options</Typography>}
      <CustomList items={items} divider heading={"Available tournaments: "+ props.round.primaryText} listClass={' '} button itemClick={itemClick}/>
      {
        props.round.tournaments !== undefined && props.round.totalMatches !== props.round.tournaments.length ?
        <>
          <Route path={'/mega/view/:id/addTournament'} component={() => <AddTournament game={props.game} roundTitle={props.round.primaryText} megladronId={props.match.params.id}/>}/>
          <div style={{height: 48}}></div>
          <Fab position="absolute" title="Add tournament" onClick={() => history.push(props.match.url+"/addTournament")}/>
        </> : <>
          <Divider />
          <Route path={'/mega/view/:id/dividePlayers'} component={() => <PushPlayers round={props.round} />} />
          <Divider />
          <Button variant="container" color="primary" onClick={() => history.push(props.match.url+"/dividePlayers")}>Divide Players</Button>
        </>
      }
    </Paper>
  )
}

export default createFragmentContainer(withRouter(TournamentsInRounds), 
  {
    game: graphql`
      fragment TournamentsInRound_game on Game {
        _id
        customFields {
          label
          type
          options
        }
        metrics
      }
    `
  }
);