import React from 'react'
import { IconButton, Badge } from '@material-ui/core'
import CustomList from 'Components/CustomList'
import Delete from '@material-ui/icons/Delete'
import CommitRelay from 'Components/CommitRelay'
import { toastSuccess } from 'Components/Toast'
import mutation from './DeleteTournament'
import {createFragmentContainer} from 'react-relay'
import graphql from 'babel-plugin-relay/macro'
import {useHistory} from 'react-router-dom'
import makeDate from 'Utils/MakeDate'
import Tabs from 'Components/Tabs'
import useMediaQuery from '@material-ui/core/useMediaQuery';

function groupData(tournaments, deleteItem) {
  let [upcoming, resultsToBeUpdated, previous] = [[], [], []]
  let now = Date.now()
  tournaments.map(item => {
      let a = ({
        id: item._id,
        primaryText: item.gameId.title+" - "+item.title,
        secondaryText: makeDate(item.time),
        _id: item._id,
        secondaryIcon: <IconButton onClick={() => deleteItem(item._id)}><Delete /></IconButton>
      })
      if(now > +item.time && item.resultsUpdated === 0) {
        resultsToBeUpdated.push(a)
      }else if(now < +item.time ) {
        upcoming.push(a)
      }else {
        previous.push(a)
      }
      return null
    }
  )
  return [upcoming, resultsToBeUpdated, previous]
}

function Tournaments(props){
  const history = useHistory()
  const matches = useMediaQuery('(min-width:600px)');
  //console.log(props)
  if(!props.tournament){
    return <center>No Tournament avaibale</center>
  }
  let [upcoming, resultsToBeUpdated, previous] = groupData(props.tournament, deleteItem)

  function itemClick(item){
    //console.log(item)
    history.push("/tournaments/"+item.id)
  }

  function deleteItem(id) {
    if(!window.confirm("Do you wish to delete")){
      return
    }
    CommitRelay({id}, mutation, (response, errors) => {
      if(!errors){
        toastSuccess("Item deleted")
      }
    }, {
      updater: (store) => {
        store.delete(id)
      }
    })
  }

  return (
    <Tabs orientation={matches ? "vertical" : "horizontal"} tabs={[<Badge color="secondary" badgeContent={upcoming.length}>Upcoming</Badge>, <Badge color="secondary" badgeContent={resultsToBeUpdated.length}>Results to be updated</Badge>, "Completed"]}
      panels={[
        <CustomList items={upcoming} button divider heading={'Upcoming'} itemClick={itemClick}/>, 
        <CustomList items={resultsToBeUpdated} button divider heading={'Update results'} itemClick={itemClick}/>,
        <CustomList items={previous} button divider heading={'All done tournaments'} itemClick={itemClick}/>
      ]}
    />
  )
}

export default createFragmentContainer(Tournaments, {
  tournament: graphql`
    fragment Tournaments_tournament on Tournament @relay(plural: true){
      _id
      title
      totalRegistered
      time
      resultsUpdated
      gameId {
        title
      }
    }
  `
})