import React from 'react'
import {createFragmentContainer} from 'react-relay'
import graphql from 'babel-plugin-relay/macro'
import CustomList from 'Components/CustomList'
import makeDate from 'Utils/MakeDate'
import { IconButton } from '@material-ui/core'
import Delete from '@material-ui/icons/Delete'
import TournamentsInRound from './TournamentsInRound'
import CommitRelay from 'Components/CommitRelay'
import { withRouter } from 'react-router-dom'
import { snackbarSuccess } from 'Store/actions/snackbar'

const deleteRoundMutation = graphql`
  mutation RoundsOverviewMutation($data: DeleteRoundInMegladronInput!){
    deleteRoundInMegladron(data: $data) {
      _id
      title
      rounds {
        title
        tournaments {
          _id
          title
          time
        }
      }
    }
  }
`

function makeData(data, deleteClick) {
  if(data === undefined || data === null) {
    return []
  }
  //console.log(data)
  return data.map((item, index) => ({
    id: index,
    primaryText: item.title,
    secondaryText: ` Start from ${makeDate(item.startTime)} to ${makeDate(item.endTime)} and will have ${item.totalMatches} tournaments`,
    secondaryIcon: <IconButton onClick={() => deleteClick(item.title)}><Delete /></IconButton>,
    ...item
  }))
}

function RoundsOverview(props){
  
  let [selected, setSelected] = React.useState({})
  function deleteClick(title) {
    CommitRelay({data: {megladronId: props.match.params.id, roundTitle: title}}, deleteRoundMutation, (response, error) => {
      if(!error) {
        snackbarSuccess("Round deleted successfully")
      }
    })
  }

  function itemClick(item) {
    setSelected(item)
  }

  let items = makeData(props.rounds.rounds, deleteClick)

  return <div>
    {selected.id !== undefined ?  <TournamentsInRound round={selected} game={props.game}/> : null}
    <CustomList items={items} divider heading="Total Rounds" button itemClick={itemClick} selected={[selected.id]}/>
  </div>
}

export default createFragmentContainer(withRouter(RoundsOverview), {
  rounds: graphql`
    fragment RoundsOverview_rounds on Megladron {
      rounds {
        _id
        title
        startTime
        endTime
        totalMatches
        tournaments {
          _id
          time
          title
        }
      }
    }
  `

})