import React from 'react'
import { Switch, Route } from 'react-router-dom'
import Account from './Account'
import TournamentRoutes from './Tournament'
import Games from './Games'
import { makeStyles } from '@material-ui/core/styles'
import Home from './Home'
import Wallet from './Home/Wallet/DetailedReport'
import Drawer from './Drawer'
import Notification from './Notifications/Holder'
import Mega from './Mega'
import ProtectedRoute from 'Components/ProtectedRoute'
import Corousal from './Pages/Corousal/CorousalContent'
import UserManagement from './User'

const styles = makeStyles(theme => ({
  toolbar: theme.mixins.toolbar,
  container: {
    width: '100%'
  },
  content: {
    height: `100%`,
    width: '100%',
    [theme.breakpoints.up('md')]: {
      paddingLeft: 240,
    },
  }
}))

function Layout(){
  const classes = styles()
  return(
    <div>
      <Drawer />
      <div className={classes.toolbar} />
      <div className={classes.content}>
        <Switch>
          <Route path={'/account'} component={Account} />
          <ProtectedRoute path={'/tournaments'} component={TournamentRoutes} />
          <ProtectedRoute path={'/games'} component={Games}/>
          <ProtectedRoute path={'/wallet/:id'} component={Wallet} />
          <ProtectedRoute path={'/notification'} component={Notification} />
          <ProtectedRoute path={'/mega'} component={Mega} />
          <ProtectedRoute path={'/pages/corousal'} component={Corousal} />
          <ProtectedRoute path={'/management/user'} component={UserManagement} />
          <ProtectedRoute path={'/'} component={Home} />
        </Switch>
      </div>
    </div>
  )
}

export default Layout