import React from 'react'
import Button from 'Components/LoadingButton'
import Tabs from 'Components/Tabs'
import Modal from 'Components/Modal';
import {snackbarInfo} from 'Store/actions/snackbar'
import PickAndDistribute from './PickAndDistribute'
import CommitRelay from 'Components/CommitRelay'
import graphql from 'babel-plugin-relay/macro'
import {withRouter} from 'react-router-dom'
import {snackbarSuccess} from 'Store/actions/snackbar'

const randomSplitMutation = graphql`
  mutation PushInTournamentsMutation($data: DistributePlayersInput!){
    distributePlayers(data: $data) {
      message
    }
  }
`

function PushInTournament(props) {

  return <div>
    <Tabs 
      tabs={["Random distribution", "Pick winners of round", "Distribute player from round"]}
      panels={[
        <RandomDistribution round={props.round} megladronId={props.match.params.id}/>,
        <PickAndDistribute megladronId={props.match.params.id} round={props.round}/>
      ]}
      />      
  </div>
}

function RandomDistribution(props) {
  const [modal, setModal] = React.useState(false)
  const [requesting, setRequesting] = React.useState(false)
  console.log(props)
  function distributePlayers() {
    //snackbarInfo("will distribute players", Date.now())
    setModal(false)
    setRequesting(true)
    CommitRelay({data: {megladronId: props.megladronId, roundTitle: props.round.primaryText}}, randomSplitMutation, (response, error) => {
      //console.log(response, error)
      setRequesting(false)
      if(!error) {
        snackbarSuccess("Players successfully split in tournaments")
      }
    })
  }
  return <div>
    <Button isLoading={requesting} onClick={() => setModal(true)}>Randomly distribute players</Button>
    <Modal open={modal} 
      title={"Confirm"} 
      description={"This will distribute all the players from megladron to all the available tournaments in this round evenly"}
      handleClose={() => setModal(false)}
      submitText={"Yes! distribute players"}
      handleSubmit={() => distributePlayers()}
      />
  </div>
}


export default withRouter(PushInTournament)