import React from 'react'
import { TextField } from '@material-ui/core'
import Button from 'Components/LoadingButton'
import CommitRelay from 'Components/CommitRelay'
import graphql from 'babel-plugin-relay/macro'
import { snackbarInfo, snackbarError, snackbarSuccess } from 'Store/actions/snackbar'

const mutation = graphql`
  mutation LiveStreamUpdateMutation($links: String!, $id: ID!){
    addLiveStreamUrl(links: $links, id: $id) {
      _id
      liveStream
    }
  }
`

function LiveStreamUpdate(props){

  let [value, setValue] = React.useState(props.liveStream)
  let [loading, setLoading] = React.useState(false)

  function updateStreamURL(){
    if(value.length <= 10){
      return snackbarInfo("Link can't be of length < 10")
    }
    setLoading(true)
    CommitRelay({id: props.id, links: value}, mutation, (response, error) => {
      setLoading(false)
//      console.log(error, response)
      if(error) {
        snackbarError("Unable to update links")
      }else {
        snackbarSuccess("Stream url updated")
      }
    })
  }

  return (
    <div>
      <TextField label="Live Stream urls" value={value} name="liveStrem" onChange={e => setValue(e.target.value)} helperText={"If multiple enter comma seperated without space"} fullWidth/>
      <Button isLoading={loading} onClick={updateStreamURL}>Update Stream url</Button>
    </div>
  )
}

export default LiveStreamUpdate