import React from 'react'
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert from '@material-ui/lab/Alert';
//import { makeStyles } from '@material-ui/core/styles';
import {connect} from 'react-redux'
import {closeSnackbar} from 'Store/actions/snackbar'
//import Slide from '@material-ui/core/Slide'

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

// const useStyles = makeStyles((theme) => ({
//   root: {
//     width: '100%',
//   },
// }));

function CustomizedSnackbars(props) {
  //const classes = useStyles();

  const handleClick = (event, reason, id) => {
    if(reason === "clickaway"){
      return
    }
    closeSnackbar(id)
    //setOpen(true);
  };
  //console.log(props.snackbar)

  return (
    <div>
      {
        props.snackbar.data.map((item, index) => <Snackbar open={true} onClose={handleClick} anchorOrigin={{vertical: "top", horizontal: "right"}} key={item.id} style={{marginTop: (64 * index) + "px"}}>
        <Alert severity={item.type} onClose={(event, reason) => handleClick(event, reason, item.id)}>
          {item.message}
        </Alert>
      </Snackbar>)
      }
      
    </div>
  );
}

const mapStateToProps = state => ({
  snackbar: state.Snackbar
})

export default connect(mapStateToProps)(CustomizedSnackbars)