import React from 'react'
import Form from 'Components/Form'
import { TextField } from '@material-ui/core';
import LoadingButton from 'Components/LoadingButton'
import CommitRelay from 'Components/CommitRelay'
import graphql from 'babel-plugin-relay/macro'
import { toastInfo, toastSuccess } from 'Components/Toast';
import Select from 'Layout/FormBuilder/Select'

const mutation = graphql`
  mutation NotificationMutation($data: NotificationInput!) {
    sendNotification(data: $data) {
      message
    }
  }
`

function Title(props) {
  let [value, setValue] = React.useState("")
  return <TextField name="title" value={value} fullWidth onChange={e => setValue(e.target.value)} label="Title" required helperText={value.length+"/20"} error={value.length > 20}/>
}

function Description(props) {
  let [value, setValue] = React.useState("")
  return <TextField rows={3} multiline={true} label="Message" name="body" fullWidth onChange={e => setValue(e.target.value)} value={value} required helperText={value.length+"/100"} error={value.length > 100} multiline={true} rows={4}/>
}

function getDefaultURL(tournament) {
  //console.log(tournament.id)
  if(tournament.id === undefined) {
    return "https://www.gamingstars.in"
  }else {
    return `https://www.gamingstars.in/tournaments/${tournament.gameId.title}/${tournament._id}`
  }
}

const RedirectURL = ({tournament, ...props}) => {
  let [url, setUrl] = React.useState(getDefaultURL(tournament))
  React.useEffect(() => {
    setUrl(getDefaultURL(tournament))
  }, [tournament])
  return <TextField name="url" fullWidth label="URL user will be sent to" value={url} onChange={e => setUrl(e.target.url)}/>
}

function Notification(props) {

  let [loading, setLoading] = React.useState(false)
  function onSubmit(data) {
    if(data.title.length > 20 ) {
      return toastInfo("Title length should not be more than 15 characters")
    }else if(data.body.length > 100) {
      return toastInfo("Message can not exceed 100 characters")
    }else if(data.type === "Tournament" && props.tournament.id === undefined) {
      return toastInfo("You have select to send notification to tournament participants. But, did not select a tournament")
    }
    setLoading(true)
    CommitRelay({data: {...data, tournamentId: props.tournament.id}}, mutation, (response, error) => {
      setLoading(false)
      if(!error) {
        toastSuccess("Notifications sent")
      }
    })
  }

  // React.useEffect(() => {
  //   console.log(props.tournament)
  // }, [props.tournament])

  //console.log(props.tournament)

  return (
      <Form onSubmit={onSubmit} heading={'Data for notification'}>
        <Select name="type" defaultValue={"Tournament"} options={["ALL", "Tournament"]} label={"Send notification to"}/>
        <Title />
        <Description/>
        <RedirectURL tournament={props.tournament}/>
        <LoadingButton type="submit" isLoading={loading}>Send Notification</LoadingButton>
      </Form>
  )
}

export default Notification;