import React from 'react'
import Chart from 'chart.js'
import {Paper, Typography, Switch} from '@material-ui/core'

function makeDate(time) {
  let a = new Date(+time)
  return a.getDate()+"/"+(a.getMonth()+1)
}

class AreaChart extends React.Component {

  constructor() {
    super()
    this.state = {
      reportCumulative: true
    }
    this.chart = React.createRef()
    this.canvasChart = null
  }

  componentDidMount = () => {
    let ctx = this.chart.current.getContext('2d')
    this.canvasChart = new Chart(ctx, {
      type:"line",
      data: {
          labels: this.props.overview.map(item => makeDate(item.time)),
          datasets: [
            {
              label: '# of Users',
              fill: "origin",
              data: this.props.overview.map(item => item.users),
              backgroundColor: "rgba(245, 0, 87, .2)",
              borderColor: "#f50057",
              borderWidth: 2
            },
            {
              label: '# of Transactions',
              fill: "origin",
              data: this.props.overview.map(item => item.transactions),
              backgroundColor: "rgba(138, 133, 255, .2)",
              borderColor: "#8A85FF",
              borderWidth: 2
            },
            {
              label: '# of Tournaments',
              fill: "origin",
              data: this.props.overview.map(item => item.tournaments),
              backgroundColor: "rgba(244, 67, 54, .2)",
              borderColor: "#f44336",
              borderWidth: 2
            },
          ]
      },
      options: {
        layout: {
          padding: {
            left: 16,
            bottom: 16,
            top: 16,
            right: 16
          }
        }
      }
  });
  }

  switchReport = (e) => {
    //console.log(e.target.checked)
    this.setState({reportCumulative: e.target.checked})
    if(e.target.checked) {
      //generate cumulative report
      let data = [[], [], []]
      this.props.overview.forEach(item => {
        data[0].push(item.users)
        data[1].push(item.transactions)
        data[2].push(item.tournaments)
      })
      let i = 0
      this.canvasChart.data.datasets.forEach(dataset => {
        dataset.data = data[i]
        i+=1
      })
      this.canvasChart.update()
    }else {

      let data = [[0], [0], [0]]
      for(let i = 1; i < this.props.overview.length; i++) {
        data[0].push(this.props.overview[i].users - this.props.overview[i-1].users)
        data[1].push(this.props.overview[i].transactions - this.props.overview[i-1].transactions)
        data[2].push(this.props.overview[i].tournaments - this.props.overview[i-1].tournaments)
      }
      let i = 0
      this.canvasChart.data.datasets.forEach(dataset => {
        dataset.data = data[i]
        i+=1
      })
      this.canvasChart.update()
    }
  }

  render() {
    return (
      <Paper className={this.props.classes.graphContainer}>
        <Typography variant="h4" color="textSecondary" className={this.props.classes.title}> <span>Statistics of last 7 days</span> <span>
            <Typography variant="caption">Cumulate data</Typography><Switch onClick={this.switchReport} checked={this.state.reportCumulative}/>
          </span></Typography>
        {
          this.props.overview.length < 1 ?  <Typography variant="h1" color="textSecondary" className={this.props.classes.noData}>Not enough data to form chart</Typography> : 
          <canvas id="area" ref={this.chart} />
        }
    </Paper>)
  }

}

export default AreaChart