import React from 'react'
import GetUser from './GetUser'
import { Grid, Typography, Paper, Box } from '@material-ui/core'
import TransactionOperations from './TransactionOperations'

function InitiateTransaction(props) {
  let [userData, setUserData] = React.useState({})
  function userResult(data) {
    setUserData(data.getUser.user)
  }

  return <div>
    <GetUser userResult={userResult}/>
    <RenderUserDetails userData={userData} />
    {userData._id && <TransactionOperations userId={userData._id} updateUser={(user) => setUserData(user)}/> }
  </div>
}

function RenderUserDetails(props) {
  //console.log(props.userData, props)
  return <Grid container>
    <UserCard heading={"Username"} value={props.userData.username || "JonDoe@gmail.com"}/>
    <UserCard heading={"Wallet Balance"} value={props.userData.walletBalance || "-1"}/>
    <UserCard heading={"Bonus balance"} value={props.userData.bonus || "-1"}/>
    <UserCard heading={"Id"} value={props.userData._id || "Infinity"}/>
  </Grid>
}

const UserCard = props => <Grid item xs={6} sm={3} style={{padding: 8}}>
  <Box p={1} component={Paper}>
  <Typography variant="caption" color="textSecondary"> {props.heading} </Typography> <br />
  {props.value}
  </Box>
</Grid>

export default InitiateTransaction