import React from 'react'
import {Paper, Box, Grid, Typography, Avatar} from '@material-ui/core'
import {makeStyles} from '@material-ui/core/styles'
import graphql from 'babel-plugin-relay/macro'
import QueryRenderer from 'Components/QueryRenderer'
import Person from '@material-ui/icons/PersonOutline'
import SportsEsports from '@material-ui/icons/SportsEsports'
import Tournament from '@material-ui/icons/EmojiEventsRounded'
import AttatchMoney from '@material-ui/icons/AttachMoney'
import Chart from './Chart'


const query = graphql`
  query OverviewQuery {
    getOverview {
      _id
      games
      tournaments
      withdrawals
      users
      transactions
    }
    getOverviewData(days: 7) {
      overview {
        _id
        games
        tournaments
        withdrawals
        users
        transactions
        time
      }
    }
  }
`

const styles = makeStyles(theme => ({
  paper: {
    padding: theme.spacing(2)
  },
  avatar: {
    height: theme.spacing(8),
    width: theme.spacing(8),
    backgroundColor: theme.palette.primary.main
  },
  title: {
    fontWeight: 400,
    textTransform: "uppercase",
    marginTop: theme.spacing(2),
    marginBottom: theme.spacing(2),
    paddingLeft: theme.spacing(2),
    justifyContent: "space-between",
    width: "100%",
    display: "flex"
  },
  icon: {
    color: "#fff",
    fontSize: theme.spacing(4)
  },
  graphContainer: {
    marginTop: theme.spacing(1),
    padding: theme.spacing(1)
  },
  noData: {
    height: 300,
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    textTransform: "uppercase"
  }
}))

const PaperCard = ({heading, classes, icon, value}) => <Grid item md={3} sm={4} xs={6}>
  <Paper className={classes.paper}>
  <Grid container alignItems={'center'}>
    <Grid item xs={8}>
      <Typography color="textSecondary" variant="overline"> {heading} </Typography>
      <Typography variant="h2">{value}</Typography>
    </Grid>
    <Grid item xs={4}>
      <Avatar color="secondary" className={classes.avatar}>
        {icon}
      </Avatar>
    </Grid>
  </Grid>
</Paper>
</Grid>

function OverviewComponent(props) {
  const classes = styles()
  let overview = props.getOverviewData.overview
  //console.log(overview)
  return (
    <Box p={1}>
      <Grid container spacing={2}>
        <PaperCard classes={classes} icon={<SportsEsports className={classes.icon} />} heading={"Games"} value={props.getOverview.games}/>
        <PaperCard classes={classes} icon={<Tournament className={classes.icon} />} heading={"Tournaments"} value={props.getOverview.tournaments}/>
        <PaperCard classes={classes} icon={<Person className={classes.icon}/>} heading={"Users"} value={props.getOverview.users}/>
        <PaperCard classes={classes} icon={<AttatchMoney className={classes.icon}/>} heading={"Transactions"} value={props.getOverview.transactions}/>
      </Grid>
      <Chart overview={overview} classes={classes}/>  
    </Box>
  )
}

function Overview() {
  return <QueryRenderer query={query} component={data => <OverviewComponent {...data} />}/>
}

export default Overview