import React from 'react'
import Select from 'Layout/FormBuilder/Select'
import { useForm, Controller } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers';
import Button from 'Components/LoadingButton'
import * as yup from 'yup'
import FormRegular from 'Components/Styled/FormRegular'
import Input from 'Components/FormComps/InputReactHook'
import CommitRelay from 'Components/CommitRelay'
import graphql from 'babel-plugin-relay/macro'
import TextField from '@material-ui/core/TextField'
import makeDate from 'Utils/MakeDate';
import { snackbarSuccess } from 'Store/actions/snackbar';
import { FormHelperText } from '@material-ui/core';

const initiateTransactionMutation = graphql`
  mutation TransactionOperationsMutation($data: TransactionOperationInput!) {
    transactionOperation(data: $data) {
      message
      user {
        _id
        walletBalance
        bonus
      }
    }
  }
`

const schema = yup.object().shape({
  message: yup.string().required(),
  amount: yup.number().required(),
  transactionType: yup.string().required()
});

function TransactionOperations(props) {

  let [requesting, setRequesting] = React.useState(false)
  function onSubmit(data) {
    if(!window.confirm("Do you wish to perform operation")) {
      return
    }
    setRequesting(true)
    CommitRelay({data}, initiateTransactionMutation, (response, error) => {
      setRequesting(false)
      if(!error) {
        snackbarSuccess(response.transactionOperation.message)
        props.updateUser(response.transactionOperation.user)
      }
    })
  }

  const { register, handleSubmit, errors, control } = useForm({
    resolver: yupResolver(schema)
  });

  return <FormRegular component={"form"} onSubmit={handleSubmit(onSubmit)}>
    <TextField name="userId" inputRef={register} disabled={true} value={props.userId} fullWidth label={"User Id"}/>
    <Controller 
      as={<Select options={[{label: "Move balance from bonus to wallet", value: "MOVE"}, {label: "Add In wallet", value: "ADD"}, {label: "Add In Bonus", value: "BONUS"}]} label={"Transaction type"} 
      name="transactionType" />} 
      control={control} name="transactionType" />
    {Boolean(errors.transactionType) && <FormHelperText error={true}>{errors.transactionType?.message}</FormHelperText>}
    <Input errors={errors} inputRef={register} name="amount" required type="number" label="Amount"/>
    <Input errors={errors} inputRef={register} name="message" required label="Message" />
    <Button isLoading={requesting} type="submit">Initiate</Button>
  </FormRegular>
}

export default TransactionOperations