import React from 'react'
import QueryRenderer from 'Components/QueryRenderer'
import graphql from 'babel-plugin-relay/macro'
import CommitRelay from 'Components/CommitRelay'
import { useForm, Controller } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers';
import * as yup from 'yup'
import { Box, Paper, TextField, IconButton } from '@material-ui/core'
import Button from 'Components/LoadingButton'
import {makeStyles} from '@material-ui/core/styles'
import CustomList from 'Components/CustomList'
import Close from '@material-ui/icons/Close'
import {snackbarSuccess} from 'Store/actions/snackbar'

const styles = makeStyles(theme => ({
  paper: {
    padding: theme.spacing(1),
    marginBottom: theme.spacing(1),
    '& > *': {
      marginBottom: theme.spacing(1)
    }
  }
}))

const query = graphql`
  query CorousalContentQuery {
    page(title: "corousal"){
      title
      content
    }
  }
`

const mutation = graphql`
  mutation CorousalContentMutation($data: PageInput!){
    adminControlled(data: $data) {
      message
      page {
        title
      }
    }
  }
` 

const schema = yup.object().shape({
  label: yup.string().required(),
  btnText: yup.string().required(),
  link: yup.string().required(),
  imgPath: yup.string().required()
}); 

function CorousalContent(props){
  return <QueryRenderer query={query} component={(data) => <CorousalContentRender {...data}/>} errorComponent={(data) => <CorousalContentRender {...data}/>}/>
}

const Input = props => <TextField  error={Boolean(props.errors[props.name])} helperText={props.errors[props.name]?.message} fullWidth {...props}/>

function CorousalContentRender(props){
  let [page, setPage] = React.useState(props.page || {content: [], title: "corousal"})
  let [updating, setUpdating] = React.useState(false)
  const classes = styles()
  const { register, handleSubmit, errors, control } = useForm({
    resolver: yupResolver(schema)
  });

  function onSubmit(data) {
    page.content.push(data)
  }
  let items = []
  if(typeof page.content === "string") {
    setPage({...page, content: JSON.parse(page.content)})
  }

  function removeItem(label) {
    setPage({...page, content: page.content.filter(item => item.label !== label)})
  }

  try {
    items = page.content.map((item, index) => ({
      primaryText: item.label,
      id: index,
      secondaryText: `${item.btnText} | ${item.link}, ${item.imgPath}`,
      secondaryIcon: <IconButton onClick={() => removeItem(item.label)}> <Close /> </IconButton>
    }))
  }catch(err) {
    console.log(err)
  }

  function updateCorousal() {
    if(!window.confirm("Do you wish to update")){
      return
    }
    setUpdating(true)
    CommitRelay({data: {title: page.title, content: JSON.stringify(page.content)}}, mutation, (response, error) => {
      console.log(response, error)
      setUpdating(false)
      if(!error) {
        snackbarSuccess("Page updated!")
      }
    })
  }

  return <Box p={1}>
    <Paper className={classes.paper}>
      Corousal Views  
      <CustomList items={items} listClass={' '} divider/>
      <Button isLoading={updating} onClick={updateCorousal}>Update in DB</Button>
    </Paper>

    <Paper component="form" onSubmit={handleSubmit(onSubmit)} className={classes.paper}>
      <Input label="Title *" errors={errors} name="label" inputRef={register}/>
      <Input errors={errors} label="Button Text *" name="btnText" inputRef={register} />
      <Input errors={errors} label="Link *" name="link" inputRef={register} />
      <Input errors={errors} label="Image Link *" name="imgPath" inputRef={register} />
      <Button isLoading={false} type="submit">Push Slide</Button>
    </Paper>
  </Box>
}

export default CorousalContent