import React from 'react'
import Nav from './Navigation'
import { Link} from 'react-router-dom'
import {List, ListItem, ListItemText, ListSubheader, Collapse, ListItemIcon, ListItemSecondaryAction} from '@material-ui/core'
import ExpandMore from '@material-ui/icons/ExpandMore'
import ExpandLess from '@material-ui/icons/ExpandLess'
import {makeStyles} from '@material-ui/core/styles'

const styles = makeStyles(theme => ({
  listItem: {
    '& > *' : {
      fontSize: 14,
      paddingLeft: 18
    }
  },
  selected: {
    color: theme.palette.primary.main,
    background: "transparent !important"
  }
}))

const ExpansionPanel = props => {
  let [opened, setOpened] = React.useState(false)

  return <React.Fragment><ListItem button onClick={() => setOpened(!opened)}>
    <ListItemIcon>{props.icon}</ListItemIcon>
    <ListItem>{props.heading}</ListItem>
    <ListItemSecondaryAction> {opened ? <ExpandLess/> : <ExpandMore/> }</ListItemSecondaryAction>
  </ListItem>
  <Collapse in={opened} unmountOnExit>
    <List style={{padding: 0}}>
      {
        props.item.items.map((el, index) =>  <RenderItem key={index} item={el}/>)
      }
    </List>
  </Collapse>
  </React.Fragment>
}

const RenderItem = props => {

  const classes = styles()
  //console.log(props)

  if(props.item.items === undefined) {
    //console.log(props.item.href)
    return <ListItem key={props.item.href} button component={Link} to={props.item.href} selected={window.location.pathname === props.item.href} classes={{selected: classes.selected}}>
      <ListItemIcon>{props.item.icon}</ListItemIcon>
    <ListItemText className={classes.listItem}> {props.item.title} </ListItemText>
    <ListItemSecondaryAction> {props.item.secondaryIcon} </ListItemSecondaryAction>
  </ListItem>
  }else {
    return <ExpansionPanel heading={props.item.title} icon={props.item.icon} item={props.item}/>
  }
}

//const RenderItem = withRouter(RenderItems)

const CustomList = props => <List subheader={<ListSubheader>{props.heading}</ListSubheader>}>
  {
    props.items.map(item => <RenderItem item={item} key={item.title}/>)
  }
</List>

function Navigation(props){
  return <>
  { 
    Nav.map(item => <CustomList key={item.subheader} heading={item.subheader} items={item.items}/>)
  }
  </>
}

export default Navigation