import React from 'react'
import {Paper, TextField, Typography} from '@material-ui/core'
import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers';
import Button from 'Components/LoadingButton'
import {makeStyles} from '@material-ui/core/styles'
import * as yup from 'yup'
import CommitRelay from 'Components/CommitRelay'
import graphql from 'babel-plugin-relay/macro'
import { snackbarSuccess } from 'Store/actions/snackbar';

const styles = makeStyles(theme => ({
  form: {
    '& > *': {
      marginBottom: theme.spacing(1)
    }
  }
}))

const addUserMutation = graphql`
  mutation AddPlayersMutation($id: ID!, $gamingUsername: String!, $group: Int!){
    adminRegisterInTournament(id: $id, gamingUsername: $gamingUsername, group: $group) {
      tournament {
        totalRegistered
        _id
        registered {
          gamingUsername
          group
        }
      }
    }
  }
`

const schema = yup.object().shape({
  gamingUsername: yup.string().required(),
  group: yup.number().required().integer()
});

const Input = ({errors, ...props}) => <TextField error={Boolean(errors[props.name])} helperText={errors?.[props.name]} fullWidth required {...props}/>

function AddPlayers(props) {

  let [request, setRequest] = React.useState(false)
  const { register, handleSubmit, errors } = useForm({
    resolver: yupResolver(schema)
  });
  const classes = styles()

  function onSubmit(data) {
    if(!window.confirm("DO you wish to add this player")){
      return
    }
    setRequest(true)
    CommitRelay({id: props.id, ...data}, addUserMutation, (response, error) => {
      setRequest(false)
      if(!error) {
        snackbarSuccess("Player registered successfully")
      }
    })
  }

  return <form onSubmit={handleSubmit(onSubmit)} className={classes.form}>
    <Typography variant="h4">Add Players</Typography>
    <Input errors={errors} name="gamingUsername" label={"Gaming username"} required inputRef={register}/>
    <Input errors={errors} name="group" type="number" label={"Group"} required inputRef={register}/>
    <Button type="submit" isLoading={request}>Add User</Button>
  </form>
}

export default AddPlayers