import store from 'Store'

export const snackbarSuccess = (message, id) => store.dispatch({
  type: "SNACKBAR::ADD",
  message,
  id: id || Date.now(),
  snackbarType: "success"
})


export const snackbarError = (message, id) => store.dispatch({
  type: "SNACKBAR::ADD",
  message,
  id: id || Date.now(),
  snackbarType: "error"
})

export const snackbarWarning = (message, id) => store.dispatch({
  type: "SNACKBAR::ADD",
  message,
  id: id || Date.now(),
  snackbarType: "warning"
})

export const snackbarInfo = (message, id) => store.dispatch({
  type: "SNACKBAR::ADD",
  message,
  snackbarType: "info",
  id: id || Date.now()
})

export const closeSnackbar = (id) => store.dispatch({
  type: "SNACKBAR::CLOSE",
  id
})