import React from 'react'
import TextField from '@material-ui/core/TextField'
import Switch from '@material-ui/core/Switch'
import { useForm, Controller } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers';
import * as yup from 'yup'
import {DateTimePicker} from '@material-ui/pickers'
import { connect } from 'react-redux'
import { resetData } from 'Store/actions/AddTournament'
import { snackbarSuccess } from 'Store/actions/snackbar'
import { useHistory } from 'react-router-dom'
import graphql from 'babel-plugin-relay/macro'
import CommitRelay from 'Components/CommitRelay'
import { Paper, Button, Box } from '@material-ui/core';
import {makeStyles} from '@material-ui/core/styles'

const query = graphql`
  mutation TournamentBasicMutation($data: TournamentInput) {
    addTournament(tournamentFields: $data){
      tournament {
        _id
        title
      }
    }
  }
`


const styles = makeStyles(theme => ({
  form: {
    padding: theme.spacing(1),
    '& > *': {
      marginBottom: theme.spacing(1)
    }
  }
}))

const schema = yup.object().shape({
  title: yup.string().required(),
  description: yup.string().required(),
  maxPlayers: yup.number().positive().integer().required(),
  image: yup.string(),
  entryFee: yup.number().integer().required(),
  maxGroups: yup.number().positive().integer(),
  time: yup.string().required(),
  prizePool: yup.number().required()
}); 

const Input = props => <TextField  error={Boolean(props.errors[props.name])} helperText={props.errors[props.name]?.message} fullWidth {...props}/>

function TournamentBasic(props) {

  let history = useHistory()

  function onSubmit(data){
    let prizes = props.tournament.prizes;
    let customFields = props.tournament.customFields
    let game = props.tournament.game
    if(!prizes || prizes.length === 0){
      if(!window.confirm("Are you sure about a tournament without prizes? If you forgot to add. It's on second screen")){
        return
      }
    }
    let inputPrizes = prizes === undefined ? [] : prizes.map(p => ({prize: +p.prize, position: p.position}))
    //console.log(data)
    //return
    // for(let key in customFields){
    //   customFieldsInput.push({
    //     label: key,
    //     value: customFields[key]
    //   })
    // }
    //console.log(prizes, customFields, game, data)
    CommitRelay({
      data: {
        gameId: game._id,
        customFields: customFields,
        metricsPoints: props.tournament.metrics,
        prizes: inputPrizes,
        ...data,
        multipleRegistration: data.multipleRegistration ? 1 : 0
      }
    }, query, (response, errors) => {
      if(!errors){
        snackbarSuccess("Tournament created succefully", Date.now())
        //history.push("/tournaments")
      }
    })
  }

  const { register, handleSubmit, errors, control } = useForm({
    resolver: yupResolver(schema)
  });
  const classes = styles()
  
  //date.setDate(+date.getDate() + 1)
  //console.log(date.getMonth(), date.getDate())
  //let time = date.getFullYear()+"-"+(date.getMonth() < 10 ? "0"+date.getMonth()  : date.getMonth())+"-"+(date.getDate() < 10 ? "0"+date.getDate()  : date.getDate())
  //console.log(time, props.tournament.game.image)
  return (
    <Box p={1}>
      <Paper onSubmit={handleSubmit(onSubmit)} component="form" className={classes.form}>
        <Input inputRef={register} errors={errors} label={'Tournament title'} name="title" fullWidth required defaultValue={props.tournament.game.title+" Tournament #"+props.tournament.game.totalTournaments} helperText={'Default values are cool. But, rename it if you can'}/>
        <div>Allow multiple registration <Switch inputRef={register} name="multipleRegistration"/></div>
        <Controller as={<DateTimePicker name="time" fullWidth disablePast/>} control={control} name="time" />
        <Input inputRef={register} errors={errors} label={'Entry Fee'}   name="entryFee" type="number" required/>
        <Input inputRef={register} errors={errors} type="number" name="maxPlayers" label={'Max Participants'} type="number" required/>
        <Input inputRef={register} errors={errors} name="image"  label={"Image URL"} defaultValue={props.tournament.game.image+" "}/>
        <Input inputRef={register} errors={errors} type="number" name="maxGroups"  label={"Max groups that could be formed"} helperText={"If no grouping enter same number as max players"} required/>
        <Input inputRef={register} errors={errors} type="number" name="prizePool" label={"Prize pool"} required />
        <Input inputRef={register} errors={errors} label={'Tournament Description'} multiline rows={5} name="description" helperText={"Add things like rules or anything specific"} required/>
        <Box p={1}>
          <Button
            onClick={props.handleBack}
            >
            Back
          </Button>
          <Button variant="contained" color="primary" type={'submit'}>
            Submit
          </Button>
        </Box>
      </Paper>
    </Box>
  )
}

const mapStateToProps = state => ({
  tournament: state.AddTournament
})

const mapDispatchToProps = dispatch => ({
  resetData: () => dispatch(resetData()),
  snackbarSuccess: msg => dispatch(snackbarSuccess(msg))
})

export default connect(mapStateToProps, mapDispatchToProps)(TournamentBasic)