import React from 'react'
import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers';
import Button from 'Components/LoadingButton'
import * as yup from 'yup'
import { TextField } from '@material-ui/core';
import CommitRelay from 'Components/CommitRelay'
import graphql from 'babel-plugin-relay/macro'
import {makeStyles} from '@material-ui/core/styles'
import {snackbarSuccess} from 'Store/actions/snackbar'

const styles = makeStyles(theme => ({
  form: {
    '& > *' :{
      marginBottom: theme.spacing(1)
    }
  }
}))

const distributePlayers = graphql`
  mutation PickAndDistributeMutation($data: FindPlayersWithPointsInput!) {
    findPlayersWithPoints(data: $data) {
      message
    }
  }
`


const schema = yup.object().shape({
  topTeams: yup.number().positive().required(),
  roundTitle: yup.string().required(),
  secondMetrics: yup.string().required()
});

const Input = ({errors, ...props}) => <TextField error={Boolean(errors[props.name])} helperText={errors?.[props.name]} required fullWidth {...props}/>

function PickAndDistribute(props) {

  const classes = styles()
  const {register, errors, handleSubmit} = useForm({
    resolver: yupResolver(schema)
  })

  function onSubmit(data) {
    if(!window.confirm("Confirm your selection")) {
      return 
    }
    CommitRelay({data: {...data, megladronId: props.megladronId}}, distributePlayers, (response, error) => {
      if(!error){
        snackbarSuccess(response.findPlayersWithPoints.message)
      }
    })
  }

  return <div>
    <form onSubmit={handleSubmit(onSubmit)} className={classes.form}>
      <Input inputRef={register} name="topTeams" label="Top teams to pick" errors={errors} />
      <Input inputRef={register} value={props.round.primaryText} disabled={true} name="roundTitle" label="Round title from where players to get" errors={errors} />
      <Input inputRef={register} name="secondMetrics" label={"Metrcis to choose when points clash"} errors={errors} />
      <Button type="submit">Seperate Players</Button>
    </form>
  </div>
}

export default PickAndDistribute